@import "../config/text.css";

.timer {
    width: 30px;
    height: 30px;
    object-fit: contain;
    margin-right: 10px;
}

.timer-div {
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    font-size: 20px;
    justify-content: center;
    padding: 0;
    line-height: 0;
}

@media only screen and (max-width: 480px) {
    .timer-div {
        margin-right: 10px;
    }
}
