@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --dark_purple: #0C0032;
    --medium_purple: #190061;
    --regular_purple: #240090;
    --bright_purple: #3500D3;
    --black: #282828;
    --light_black: #282c34;
    --red: #de4b4b;
}

body > #root > div > div {
}

.game-div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--black);
    width: 100%;
    height: 100%;
}

.board-div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

* {
    font-family: 'Bebas Neue', cursive;
}

.button-form {
    display: flex;
    flex: 1 1;
    justify-content: center;
}

.container {
    display: flex;
    flex: 1 1;
    padding: 0;
    margin: 0;
}

.navbar-div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    background-color: var(--regular_purple);
    color: white;
    width: 100%;
    padding: 5px 10px;
}

.title-div {
    display: flex;
    flex: 1 1;
    justify-content: center;
    margin: 0;
    padding: 0;
    line-height: 0px;
    font-size: 30px;
}

@media only screen and (max-width: 480px) {
    .navbar-div {
        display: flex;
        flex-direction: column;
        padding: 30px 0;
    }

    .title-div {
        font-size: 40px;
    }
}

.level-button {
    background-color: var(--black);
    padding: 5px 25px;
    font-size: 20px;
    color: white;
    margin-left: 10px;
    border: solid transparent;
    outline: none;
}


.flag-img{
    width: 30px;
    height: 30px;
    object-fit: cover;
    margin-right: 10px;
}

.flags-remaining-div {
    display: flex;
    flex: 1 1;
    flex-direction: row;
    align-items: center;
    font-size: 20px;
    justify-content: center;
    padding: 0;
    line-height: 0;
}

@media only screen and (max-width: 480px) {
    .flags-remaining-div {
        margin-left: 10px;
    }
}

.timer {
    width: 30px;
    height: 30px;
    object-fit: contain;
    margin-right: 10px;
}

.timer-div {
    display: flex;
    flex: 1 1;
    flex-direction: row;
    align-items: center;
    font-size: 20px;
    justify-content: center;
    padding: 0;
    line-height: 0;
}

@media only screen and (max-width: 480px) {
    .timer-div {
        margin-right: 10px;
    }
}

.board {
    padding: 10px;
    background-color: var(--light_black);
    margin: 20px;
}

.row {
    display: flex;
    flex-direction: row;
}


.bomb {
    background-color: var(--red);
}

.cell {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: solid transparent;
    margin: 2px 2px;
    color: white;
}

.easy {
    width: 34px;
    height: 34px;
}

.hard {
    width: 18px;
    height: 18px;
}

.hidden {
    background-color: var(--regular_purple);
}
.hidden:hover {
    background-color: var(--medium_purple);
}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.shown-number {
    background-color: var(--bright_purple);
}

.shown-zero {
    background-color: var(--dark_purple);
}

@media only screen and (max-width: 480px) {
    .easy {
        width: 25px;
        height: 25px;
    }

    .hard {
        width: 12px;
        height: 12px;
    }
}


.button-div {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.endscreen {
    background-color: var(--bright_purple);
    width: 300px;
    height: 150px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -150px;
    box-shadow: 20px 20px 30px var(--light_black);
    color: white;
    text-align: center;
    line-height: 0;
}

.title {
    font-size: 30px;
}

